import Navbar from "./Navbar";
import { useState } from "react";
import { uploadFileToIPFS, uploadJSONToIPFS } from "../nftdotstorage";

import MarketplaceJSON from '../Marketplace.json';
import { useLocation } from "react-router";

export default function SellNFT () {
    //constants... pay attention, there's a lot going on here
    const [formParams, updateFormParams] = useState({ name: '', description: '', donationPercentage: '', price: '', fileURL: null});
    const [fileURL, setFileURL] = useState(null);
    const ethers = require("ethers");
    const [message, updateMessage] = useState('');
    const location = useLocation();

    async function disableButton() {
        const listButton = document.getElementById("list-button")
        listButton.disabled = true
        listButton.style.backgroundColor = "grey";
        listButton.style.opacity = 0.3;
    }

    async function enableButton() {
        const listButton = document.getElementById("list-button")
        listButton.disabled = false
        listButton.style.backgroundColor = "#A500FF";
        listButton.style.opacity = 1;
    }

    //This function uploads the NFT image to IPFS
    async function OnChangeFile(e) {
        var file = e.target.files[0];
        //check for file extension
        try {
            //upload the file to IPFS through nft.storage
            disableButton();
            updateMessage("Uploading image.. please dont click anything!")
            const response = await uploadFileToIPFS(file);
            if(response.success === true) {
                enableButton();
                updateMessage("")
                console.log("Uploaded image to Pinata: ", response.pinataURL)
                setFileURL(response.pinataURL);
            }
        }
        catch(e) {
            console.log("Error during file upload", e);
        }
    }

    //This function uploads the metadata to IPFS
    async function uploadMetadataToIPFS() {
        const {name, description, price} = formParams;
        //Make sure that none of the fields are empty
        if( !name || !description || !price || !fileURL)
        {
            updateMessage("Please fill all the fields!")
            return -1;
        }
        //the info below is used to store our data
        const nftJSON = {
            name, description, price, image: fileURL
        }
        //attempt to upload
        try {
            //upload the metadata JSON to IPFS
            const response = await uploadJSONToIPFS(nftJSON);
            if(response.success === true){
                console.log("Uploaded JSON to Pinata: ", response)
                return response.pinataURL;
            }
        }
        //upload failed, log the error
        catch(e) {
            console.log("error uploading JSON metadata:", e)
        }
    }
    //create and list an NFT
    async function listNFT(e) {
        e.preventDefault();

        //Upload data to IPFS
        try {
        
            const metadataURL = await uploadMetadataToIPFS();
            if(metadataURL === -1)
                return;
            //connect to the user's wallet
            console.log("Connecting to wallet");
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            //user signs to authorize the connection
            console.log("Getting signature");
            const signer = provider.getSigner();
            disableButton();
            updateMessage("Uploading NFT (takes one block confirmation).. please dont click anything!")
            //Pull the deployed contract instance
            let contract = new ethers.Contract(MarketplaceJSON.address, MarketplaceJSON.abi, signer);
            console.log("awaiting approval");
            //give smart contract permission to transfer the token
            await contract.setApprovalForAll(MarketplaceJSON.address, true);
            alert("Awaiting Approval, please click OK when the transaction confirms");
            //massage the params to be sent to the create NFT request
            const price = ethers.utils.parseUnits(formParams.price, 'ether');
            const donationCut = formParams.donationPercentage;
            //let listingPrice = await contract.getListPrice();
            //mutate our listingPrice to a string
            //listingPrice = listingPrice.toString();
            //actually create the NFT and list it
            let transaction = await contract.createToken(metadataURL, donationCut ,price, { value: price });
            //wait until the process completews
            await transaction.wait();
            //use a pop-up alert to tell the user they've succeeded
            alert("Congratulations, you have successfully minted and listed your NFT through Chosen Sanctuary!!!");
            //return to normal
            enableButton();
            updateMessage("");
            updateFormParams({ name: '', description: '', price: ''});
            window.location.replace("/")
        }
        //if an error happens, display it to the user
        catch(e) {
            alert( "Upload error", e);
            console.log("error", e);
            updateMessage("");
        }
    }
    //mint an NFT directly to the user's wallet
    async function mintNFT(e) {
        e.preventDefault();
        //Upload data to IPFS
        try {
            const metadataURL = await uploadMetadataToIPFS();
            if(metadataURL === -1)
                return;
            //connect to the user's wallet
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            //user signs to authorize the connection
            const signer = provider.getSigner();
            console.log("Connected to wallet")
            disableButton();
            updateMessage("Uploading NFT (takes one block confirmation).. please dont click anything!");
            //Pull the deployed contract instance
            let contract = new ethers.Contract(MarketplaceJSON.address, MarketplaceJSON.abi, signer);
            console.log("Connected to contract");
            //parse the price so that Solidity can understand it
            var listingPrice = await ethers.utils.parseUnits(formParams.price, 'ether');
            console.log("Took listing price");
            listingPrice = listingPrice.toString();
            console.log("Converted to string");
            //actually create the NFT
            let transaction = await contract.createTokenUnlisted(metadataURL, listingPrice, { value: listingPrice});
            console.log("Making transaction");
            //wait until the operation finishes
            await transaction.wait();
            //inform the user of a succussful result
            alert("Congratulations, you have successfully minted an NFT through Chosen Sanctuary!!!");
            //reset the page to normal
            enableButton();
            updateMessage("");
            updateFormParams({ name: '', description: '', price: ''});
            window.location.replace("/")
        }
        //if an error occurs, display the error to the user
        catch(e) {
            alert( "Upload error", e);
            console.log("Error", e);
        }
    }
    console.log("Working", process.env);
    console.log("Form params", formParams);
    //render the jsx below to the page
    return (
        <div className="">
        <Navbar></Navbar>
        <div className="flex flex-col place-items-center mt-10" id="nftForm">
            <form className="bg-white shadow-md rounded px-8 pt-4 pb-8 mb-4">
            <h3 className="text-center font-bold text-black-500 mb-8">Upload your NFT to the marketplace</h3>
                <div className="mb-4">
                    <label className="block text-black-500 text-sm font-bold mb-2" htmlFor="name">NFT Name</label>
                    <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="name" type="text" placeholder="Axie#4563" onChange={e => updateFormParams({...formParams, name: e.target.value})} value={formParams.name}></input>
                </div>
                <div className="mb-6">
                    <label className="block text-black-500 text-sm font-bold mb-2" htmlFor="description">NFT Description</label>
                    <textarea className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" cols="40" rows="5" id="description" type="text" placeholder="Axie Infinity Collection" value={formParams.description} onChange={e => updateFormParams({...formParams, description: e.target.value})}></textarea>
                </div>
                <div className="mb-6">
                    <label className="block text-black-500 text-sm font-bold mb-2" htmlFor="price">Price (in ETH)</label>
                    <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" type="number" placeholder="Min 0.001 ETH" step="0.001" value={formParams.price} onChange={e => updateFormParams({...formParams, price: e.target.value})}></input>
                </div>
                <div className="mb-6">
                    <label className="block text-black-500 text-sm font-bold mb-2" htmlFor="price">Donation Percentage</label>
                    <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" type="number" placeholder="25% If only minting, you can leave this blank" step="25" min="25" max="100" value={formParams.donationPercentage} onChange={e => updateFormParams({...formParams, donationPercentage: e.target.value})}></input>
                </div>
                <div>
                    <label className="block text-black-500 text-sm font-bold mb-2" htmlFor="image">Upload Image (&lt;500 KB)</label>
                    <input type={"file"} onChange={OnChangeFile}></input>
                </div>
                <br></br>
                <div className="text-red-500 text-center">{message}</div>
                <button onClick={listNFT} className="font-bold mt-10 w-full bg-black text-white rounded p-2 shadow-lg" id="list-button">
                    Create and List this NFT!
                </button>
                <button onClick={mintNFT} className="font-bold mt-10 w-full bg-black text-white rounded p-2 shadow-lg" id="list-button">
                    Mint Directly to your Wallet!
                </button>
            </form>
        </div>
        </div>
    )
}